//联系人
import contacts from './modules/contacts'
//发现
import me from './modules/me'

import getters from './getters.js'
import actions from './actions.js'

Vue.use(Vuex)


export default new Vuex.Store({
    
    /*actions,
    getters,*/
    modules: {
        base,
        chat,
        contacts,
        me
    },
    // getters,
    strict: true
})



