<template>
    <transition name="fade">
        <div class="houseInfo" v-if="show">
            <ul>
                <li v-for="item in house" >
                    <a v-bind:href="item.href">
                        <span>{{item.title}}</span>
                        <span>{{item.price}}</span>
                        <span>{{item.mianji}}</span>
                    </a>
                </li>
            </ul>
        </div>
    </transition>
</template>
<script>




export default {
    data() {
        return {
            house: [],
            show: false
        }
    },
    events: {
        'route-pipe' (_decline) {
            this.decline = _decline
            this.$parent.$emit('route-pipe', _decline)
        }
    },
    methods: {

    },
    components: {

    },
    beforeCreated() {
        debugger
    },
    created() {
       debugger
            fetch('https://jasongan.cn/api/house').then((res) => {
                return res.json();
            }).then((res) => {
            res.forEach((item) => {
                this.$data.house.push(item);
            })
            document.querySelector('.indicator').classList.add("hide_it");
            this.$data.show = true;
        })
    },
    beforeUpdated() {
        
    },
    updated() {
        debugger

        function Observer(data) {
            this.data = data;
            this.walk(data);
        }

        Observer.prototype.walk = function(obj) {
            var val, _this = this;
            Object.keys(obj).forEach((key) => {
                val = obj[key];
                if(typeof val === "object"){
                    new Observer(val)
                }
                _this.convert(key, val);
            })
        } 

        Observer.prototype.convert = function(key, val) {
            Object.defineProperty(this.data, key, {
                configurable: true,
                enumarable: true,
                get: function() {
                    debugger
                    return val;
                },
                set: function(newVal) {
                    debugger
                    console.log('set');
                    console.log("新的"+key+"="+newVal);

                    if(typeof newVal === 'object') {
                        new Observer(newVal);
                    }

                    val = newVal;

                }

            })
        }


        let data = {
            user: {
                name: "hello world",
                age: "24"
            },
            address: {
                city: "beijing"
            }
        };
        debugger
        var app = new Observer(data);
        console.log(app.data.user);

        app.data.user = {name: "123", age:"23"};

        console.log(app.data.user);
    },
    beforeMounted() {
        debugger
    },
    mounted() {
        debugger
         document.querySelector('.indicator').classList.remove("hide_it");
    }
}
</script>
<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
.houseInfo li {
    margin: 10px;
    text-align: center;
}
.houseInfo li span {
    margin-right: 20px;
}
</style>



